body {
  font-family: Nunito, sans-serif;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 0;
  }
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  color: #fff;
  background-color: #0e1217;
  position: relative;
}

.wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.section-padding-small {
  padding: 2rem 2.2rem;
}

.subheadline {
  font-size: 2.6rem;
}

.display-none {
  display: none;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

.header {
  height: auto;
}

.header__box {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0 2.2rem;
  display: flex;
}

.section__form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5rem;
  margin-bottom: 3rem;
  padding: 2rem 2.2em;
  display: flex;
}

.section__form-text {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-bottom: 3rem;
  display: flex;
}

.section__form-text svg {
  width: 4.8rem;
  height: 4.8rem;
}

.section__form-text p {
  text-align: center;
  font-size: 1.6rem;
}

@media screen and (width >= 768px) {
  .header {
    padding: 0 2.5rem;
    position: relative;
  }

  .header__box img {
    content: url("Lightning-md.ca98a23a.png");
    width: 30rem;
    height: 30rem;
  }

  .section__form {
    padding: 3rem 2.5em;
  }

  .section__form-box {
    width: 80%;
    margin: 0 auto;
  }

  .section__form-box img {
    width: 35rem;
    height: 35rem;
  }

  .section__form-text p {
    font-size: 1.8rem;
  }
}

@media screen and (width >= 992px) {
  .header, .section__form {
    padding: 4rem 7em;
  }

  .section__form-box {
    width: 60%;
  }

  .section__form-text p {
    font-size: 1.9rem;
  }
}

.btn {
  cursor: pointer;
  background-color: #0352c9;
  border: #0000;
  border-radius: 8px;
  width: auto;
  padding: 1.1rem 2.3rem;
  transition: background-color .3s;
}

.btn:hover {
  background-color: #408cff;
}

.btn:focus {
  border: 1px solid #408cff;
  outline: none;
}

.btn a {
  color: #fff;
  letter-spacing: 1px;
  font-size: 1.6rem;
  font-weight: 700;
  text-decoration: none;
}

.btn a:focus {
  border: none;
  outline: none;
}

.exit-btn {
  cursor: pointer;
  border-radius: 8px;
  padding: 1rem;
  position: fixed;
  top: 3rem;
  right: 3rem;
}

.exit-btn:hover .exit-btn__bars:after, .exit-btn:hover .exit-btn__bars:before {
  box-shadow: inset 0 0 50px -20px #a5c4f3;
}

.exit-btn:focus {
  border: 1px solid #fff;
  outline: none;
}

.exit-btn__box {
  width: 40px;
  height: 40px;
  position: relative;
}

.exit-btn__bars:before, .exit-btn__bars:after {
  content: "";
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  transition: box-shadow .3s;
  position: absolute;
  top: 50%;
}

.exit-btn__bars:before {
  left: 0;
  transform: rotate(45deg);
}

.exit-btn__bars:after {
  right: 0;
  transform: rotate(-45deg);
}

@media screen and (width >= 768px) {
  .btn {
    padding: 1.3rem 2.3rem;
  }

  .btn a {
    font-size: 1.8rem;
  }
}

.form {
  background-color: #141a22;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  gap: 3.6rem;
  padding: 3.6rem 2.3rem;
  display: flex;
}

.form__box {
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  font-size: 1.6rem;
  display: flex;
}

.form__box input {
  color: #fff;
  background-color: #222d39;
  border: #0000;
  border-radius: 8px;
  padding: 1.5rem;
}

.form__box input:focus {
  border: 1px solid #a5c4f3;
  outline: none;
}

.form .btn {
  width: 50%;
}

.form .err-msg {
  color: #ff6795;
}

.form .error-input {
  background-color: #560f24;
  border: 1px solid #ff6795;
}

@media screen and (width >= 768px) {
  .form label {
    font-size: 1.8rem;
  }
}

@media screen and (width >= 992px) {
  .form label {
    font-size: 1.9rem;
  }
}

.logo {
  color: #fff;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  font-size: 2rem;
  font-weight: 700;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.logo img {
  width: 3rem;
  height: 3rem;
}

.logo:hover {
  color: #a5c4f3;
}

.nav__buttons {
  z-index: 1000;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2.2em;
  display: flex;
  position: sticky;
  top: 0;
}

.nav__buttons .burger-btn {
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 8px;
  padding: 1rem;
}

.nav__buttons .burger-btn:focus {
  border: 1px solid #fff;
  outline: none;
}

.nav__buttons .burger-btn:hover .burger-btn__bars:before {
  width: 100%;
}

.nav__buttons .burger-btn__box {
  width: 40px;
  height: 30px;
  position: relative;
}

.nav__buttons .burger-btn__bars, .nav__buttons .burger-btn__bars:before, .nav__buttons .burger-btn__bars:after {
  content: "";
  background-color: #fff;
  height: 2px;
  transition: width .3s;
  position: absolute;
  right: 0;
}

.nav__buttons .burger-btn__bars {
  width: 100%;
  top: 0;
}

.nav__buttons .burger-btn__bars:before {
  width: 66%;
  top: 13px;
}

.nav__buttons .burger-btn__bars:after {
  width: 100%;
  top: 27px;
}

.nav-desktop {
  display: none;
}

.nav-mobile {
  background-color: #06162d;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  transition: all .5s cubic-bezier(.68, -.55, .265, 1.55);
  display: flex;
  transform: translateX(200%);
}

.nav-mobile--active {
  transform: translateX(0);
}

.nav-mobile .nav__link:before {
  content: "";
  background-color: #a5c4f3;
  width: 2px;
  height: 0;
  transition: height .3s;
  position: absolute;
  bottom: 0;
  left: -.7rem;
}

.nav-mobile .nav__link:hover {
  color: #a5c4f3;
}

.nav-mobile .nav__link:hover:before {
  height: 100%;
}

.nav {
  z-index: 10000;
  position: fixed;
  top: 0;
}

.nav__links {
  flex-direction: column;
  gap: 3rem;
  display: flex;
}

.nav__link {
  color: #fff;
  font-size: 1.9rem;
  text-decoration: none;
  transition: color .3s;
  position: relative;
}

@media screen and (width >= 768px) {
  .nav__buttons {
    display: none;
  }

  .nav-desktop {
    margin-bottom: 4rem;
    padding: 1rem 2.2rem;
    display: block;
    position: sticky;
    top: 0;
  }

  .nav__links {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: .5rem;
  }

  .nav__links-box {
    align-items: center;
    gap: 3rem;
    display: flex;
  }

  .nav__link {
    position: relative;
  }

  .nav__link:before {
    content: "";
    background-color: #a5c4f3;
    width: 0;
    height: 2px;
    transition: width .3s;
    position: absolute;
    bottom: -.2rem;
    left: 0;
  }

  .nav__link:hover {
    color: #a5c4f3;
  }

  .nav__link:hover:before {
    width: 100%;
  }
}

@media screen and (width >= 992px) {
  .nav-desktop {
    padding: 2rem 7rem;
  }

  .nav__links {
    font-size: 2rem;
  }

  .nav__links-box {
    gap: 5rem;
  }
}

.overlay {
  z-index: 10000;
  background-color: #040b15;
  width: 100vw;
  height: 100vh;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay .fadeIn {
  animation-name: fadeIn;
}

.overlay .fadeOut {
  animation-name: fadeOut;
}

.success-msg-window {
  z-index: 10000;
  width: 60vw;
  transition: all .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.success-msg-window__box {
  color: #a5c4f3;
  background-color: #141a22;
  border-radius: 8px;
  flex-direction: column;
  gap: 2rem;
  padding: 1.2rem 2rem;
  display: flex;
  position: relative;
  box-shadow: inset 0 0 18px 1px #a5c4f340;
}

.success-msg-window__box h3 {
  letter-spacing: 1.445px;
  font-size: 1.9rem;
}

.success-msg-window__box p {
  font-size: 1.6rem;
}

.success-msg-window__box img {
  content: "";
  position: absolute;
  top: -7rem;
  right: -5.5rem;
}

.success-msg-window__box .btn {
  align-self: end;
  width: 40%;
}

@media screen and (width >= 768px) {
  .success-msg-window__box {
    gap: 2.2rem;
    padding: 2.5rem 3rem;
  }

  .success-msg-window__box h3 {
    font-size: 2.2rem;
  }

  .success-msg-window__box p {
    font-size: 1.7rem;
  }
}

